* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

.main-item:not(:last-child) {
  margin-bottom: 40px;
}

.main-link {
  color: #0a1029;
  text-shadow: 1px 1px #fff;
  cursor: pointer;
  background-color: #6cc6de;
  border: 0;
  border-radius: 100px;
  outline: 0;
  padding: 10px 15px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  transition: all .2s ease-in-out;
  box-shadow: -5px -5px 20px #cae1f6, 5px 5px 20px #6b728a;
}

.main-link:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

.main-link:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

button {
  width: 48px;
  height: 48px;
  cursor: pointer;
  color: #000;
  transform: all 300ms linear;
  border: none;
  border-radius: 50%;
  margin-top: 20px;
  font-size: 14px;
  display: inline-block;
  box-shadow: inset 0 3px 4px #fff, inset 0 -3px 4px #c4b59d;
}

button[data-start], button[data-stop] {
  margin-right: 20px;
}

button:hover {
  color: #fb0404;
  box-shadow: inset 0 3px 4px #fff, inset 0 -3px 4px #c4b59d;
}

button:active {
  background-color: #6b728a;
  position: relative;
  top: 3px;
}

